.block--a .sk-sticky {
  top: 100px;
}
@media screen and (max-width: 1024px) {
  .block--a .left-content {
    width: 100% !important;
  }
  .block--a .hide-m-form .right-content-form {
    display: none !important;
  }
}
.block--a .h3,
.block--a .h4 {
  padding-top: 1.5rem;
}
.block--a .left-content .person-quote-block {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.block--a .left-content .person-quote-block .quote-by-info {
  margin-bottom: 0;
  font-family: "Libre Franklin", sans-serif;
}
.block--a .left-content .person-quote-block .quote-by-info .person-name {
  font-weight: 600;
  font-family: "Libre Franklin", sans-serif;
}
.block--a .left-content .person-quote-block .person-img-wrap {
  width: 88px;
  height: 89px;
  margin-right: 30px;
}
.block--a .left-content .person-quote-block .person-img-wrap img {
  width: 110px;
  height: auto;
}
.block--a .left-content .person-quote-block blockquote {
  display: flex;
}
.block--a .left-content .person-quote-block blockquote::before {
  content: "";
  background-image: url("/wp-content/themes/custom_theme/assets/media/images/pictograms/quote-icon-red.webp");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  flex: 0 0 auto;
  display: inline-block;
  width: 24px;
  height: 24px;
  position: relative;
  margin-right: 12px;
  top: -5px;
}
.block--a .landing-page-form .gfield {
  margin-bottom: 30px !important;
}
.block--a .right-form .landing-page-form {
  border-radius: 2.7778rem;
}
.block--a .right-form .landing-page-form .form-subheading {
  font-size: 16px;
}
.block--a .right-form .landing-page-form form input:not(.button) {
  width: 100%;
}